"use client";

import { useEffect, useRef } from "react";
import * as echarts from "echarts";

function ChartCard({ option, title, subtitle }) {
  const ref = useRef(null);
  useEffect(() => {
    const el = ref.current;
    if (!el) return;
    const chart = echarts.init(el, undefined, { renderer: "canvas" });
    chart.setOption(option);
    const onResize = () => chart.resize();
    window.addEventListener("resize", onResize);
    return () => { window.removeEventListener("resize", onResize); chart.dispose(); };
  }, [option]);

  return (
    <div className="rounded-2xl border border-neutral-200 bg-white/80 backdrop-blur-md p-4 shadow-sm">
      <div className="flex items-center justify-between">
        <div>
          <div className="text-sm font-semibold text-gray-900">{title}</div>
          {subtitle ? <div className="text-xs text-gray-600">{subtitle}</div> : null}
        </div>
      </div>
      <div ref={ref} className="mt-3 h-56 w-full" />
    </div>
  );
}

export default function GeneratedDashboard({ charts }) {
  if (!charts || charts.length === 0) return null;
  return (
    <div className="mt-6 grid grid-cols-1 md:grid-cols-2 gap-6">
      {charts.map((c, i) => (
        <ChartCard key={i} option={c.option} title={c.title} subtitle={c.subtitle} />
      ))}
    </div>
  );
}
